@media(min-width:1440px) {
  .score {
    height: 180px;
    background-image: url('../img/SCORE.webp');
    background-repeat: no-repeat;
    background-size: cover;
    &__title {
      font-family: 'Acrom', sans-serif;
      margin: 0;
      padding-top: 20px;
      color: #fff;
      margin-left: 165px;
      text-transform: uppercase;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        bottom: -107px;
        left: 140px;
        background-color: #1c1c1c;
        width: 15px;
        height: 22px;
      }
    }
    &__description {
      padding-top: 5px;
      font-family: 'Acrom', sans-serif;
      font-size: 31px;
      margin-left: 165px;
      color: #ffae00;
      position: relative;
      &::before {
        content: 'деноминация';
        position: absolute;
        font-family: 'Acrom', sans-serif;
        color: #fff;
        top: 55px;
        font-size: 14px;
        right: 202px;
        text-transform: uppercase;
      }
    }
    &__take {
      font-family: 'Acrom', sans-serif;
      width: 111px;
      appearance: none;
      cursor: pointer;
      background-image: url('../img/arrow.webp');
      background-position: 100% 70%;
      background-repeat: no-repeat;
      background-color: #262727;
      margin-left: 232px;
      font-size: 19px;
      color: #ffffff;
      font-weight: bold;
      border: none;
      outline: none;
    }
  }
  .offer {
    height: 285px;
    background-image: url('../img/TIMELINE.webp');
    background-repeat: no-repeat;
    background-size: cover;
    &__price {
      font-family: 'Acrom', sans-serif;
      padding-top: 90px;
      margin-left: 250px;
      font-size: 18px;
      color: #fafafa;
      &:first-child {
        padding-bottom: 0;
      }
      &:nth-child(2) {
        padding: 15px 0 0 0;
      }
      &:nth-child(3) {
        padding: 15px 0 0 0;
      }
      &:last-child {
        padding: 15px 0 0 0;
      }
    }
  }
  .wrapper {
    display: flex;
    justify-content: space-between;
    margin: 0;
  }
  .pravila {
    outline: none;
    font-size: 0;
    background-image: url('../img/RULES.webp');
    width: 180px;
    height: 55px;
    background-color: transparent;
    border: none;
    margin-top: 0;
    background-repeat: no-repeat;
    cursor: pointer;
    &__pos {
      width: 241px;
    }
  }
  .pay {
    outline: none;
    font-size: 0;
    background-image: url('../img/OFFER.webp');
    width: 165px;
    height: 45px;
    margin-left: -40px;
    margin-top: 10px;
    background-color: transparent;
    border: none;
    background-repeat: no-repeat;
    cursor: pointer;
    &__pos {
      width: 150px;
    }
  }
}