@media(max-width: 1439px) {
  .main {
    &__side {
      display: none;
    }
    &__games {
      display: flex;
      width: 320px;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
    }
    &__games-list {
      display: flex;
      flex-wrap: wrap;
      margin: 0;
      padding: 0;
      list-style-type: none;
      &-items {
        width: 150px;
        margin: 0 auto;
        &--icon {
          outline: none;
          cursor: pointer;
          border: none;
          background-color: transparent;
        }
      }
    }
  }
}

@media(min-width:1440px) {
  .main {
    display: flex;
    justify-content: center;
    &__w {
      display: flex;
      flex-direction: row;
      justify-content: center;
      width: 1440px;
    }
    &__side {
      background-image: url('../img/PROIZVOD (2).webp');
      background-repeat: no-repeat;
      margin-top: -11px;
      position: relative;
      z-index: 0;
      &::before {
        content: '';
        position: absolute;
        background-image: url('../img/checked.webp');
        background-repeat: no-repeat;
        top: 50px;
        left: 22px;
        z-index: -1;
        width: 100%;
        height: 60px;
      }
    }
    &__games {
      &-list {
        display: flex;
        flex-wrap: wrap;
        width: 755px;
        margin: 0;
        padding: 0;
        list-style-type: none;
        &-items {
          margin-top: 27px;
          margin-left: 21px;
          border-radius: 20px;
          position: relative;
          &:nth-child(2) {
            margin-left: 15px;
          }
          &:nth-child(3) {
            margin-left: 15px;
          }
          &:nth-child(5) {
            margin-left: 15px;
          }
          &:nth-child(6) {
            margin-left: 15px;
          }
          &:nth-child(8) {
            margin-left: 15px;
          }
          &:nth-child(9) {
            margin-left: 15px;
          }
          &:nth-child(10) {
            z-index: 22;
          }
          &:nth-child(11) {
            margin-left: 15px;
            z-index: 22;
          }
          &:nth-child(12) {
            margin-left: 15px;
            z-index: 22;
          }
          &--icon {
            width: 220px;
            height: 150px;
            outline: none;
            cursor: pointer;
            border: none;
            background-color: transparent;
          }
        }
      }
    }
    &__list {
      margin: 0;
      padding: 0;
      list-style-type: none;
      &-items {
        padding: 40px 59px;
        &-btn {
          outline: none;
          border: none;
          cursor: pointer;
          background-color: transparent;
        }
        &:first-child {
          padding-top: 70px;
          padding-bottom: 0;
        }
        &:nth-child(2) {
          padding-bottom: 0;
        }
        &:nth-child(3) {
          padding-bottom: 10px;
        }
        &:last-child {
          padding: 0;
          padding-top: 20px;
          padding-left: 45px;
        }
      }
    }
  }
  .sw {
    background-image: none;
  }
  .save {
    position: relative;
    &__icon {
      position: absolute;
      bottom: -10px;
      right: 60px;
      z-index: 22;
      width: 550px;
      height: 100px;
      background-image: url('../img/SAVE.webp');
      background-repeat: no-repeat;
      background-position: center;
      outline: none;
      border: none;
      cursor: pointer;
      background-color: transparent;
      &:hover {
        background-image: url('../img/btn_save-hover.webp');
      }
    }
    &__left {
      position: absolute;
      bottom: 30px;
      left: -570px;
      z-index: 22;
      width: 30px;
      height: 30px;
      background-image: url('../img/arrow-up.webp');
      background-repeat: no-repeat;
      background-position: center;
      outline: none;
      border: none;
      cursor: pointer;
      background-color: transparent;
      transform: rotate(180deg);
      &:hover {
        background-image: url('../img/Vector.webp');
      }
    }
    &__right {
      position: absolute;
      bottom: 30px;
      right: 100px;
      z-index: 22;
      width: 30px;
      height: 30px;
      background-image: url('../img/arrow-up.webp');
      background-repeat: no-repeat;
      background-position: center;
      outline: none;
      border: none;
      cursor: pointer;
      background-color: transparent;
      &:hover {
        background-image: url('../img/Vector.webp');
        transform: rotate(360deg);
      }
    }
  }
}

