@media(max-width: 1439px) {
  .header {
    text-align: center;
  }
}

@media(min-width:1440px) {
  .header {
    width: 100%;
    &__wrap {
      display: flex;
      justify-content: center;
      &::before {
        content: '';
        display: block;
        position: absolute;
        width: 1440px;
        height: 900px;
        background-image: url('../img/main_lobbie new.webp');
        background-repeat: no-repeat;
        background-size: cover;
        z-index: -1;
      }
    }
    &__logo {
      margin-right: 870px;
      z-index: 20;
    }
    &__opros {
      width: 255px;
      height: 65px;
      margin-top: 40px;
      background-color: transparent;
      outline: none;
      border: none;
      cursor: pointer;
      background-image: url('../img/OPROS.webp');
      background-repeat: no-repeat;
      text-transform: uppercase;
      &:hover {
        background-image: url('../img/btn_opros-hover.webp');
      }
    }
  }
}