@charset "UTF-8";
.page {
  min-width: 100%;
  height: 100%;
}

.page__body {
  min-height: 100%;
  display: grid;
}

@font-face {
  font-weight: normal;
  font-family: "Acrom", sans-serif;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ofont.ru_Acrom.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 18px;
  font-style: normal;
  font-weight: normal;
  color: #000;
  background-color: white;
  background-position: top center;
  background-repeat: no-repeat;
}

img {
  max-width: 100%;
  height: auto;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.menu {
  width: 100%;
}

