@media (max-width: 1439px) {
  .header {
    text-align: center;
  }
}
@media (min-width: 1440px) {
  .header {
    width: 100%;
  }
  .header__wrap {
    display: flex;
    justify-content: center;
  }
  .header__wrap::before {
    content: "";
    display: block;
    position: absolute;
    width: 1440px;
    height: 900px;
    background-image: url("../img/main_lobbie new.webp");
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;
  }
  .header__logo {
    margin-right: 870px;
    z-index: 20;
  }
  .header__opros {
    width: 255px;
    height: 65px;
    margin-top: 40px;
    background-color: transparent;
    outline: none;
    border: none;
    cursor: pointer;
    background-image: url("../img/OPROS.webp");
    background-repeat: no-repeat;
    text-transform: uppercase;
  }
  .header__opros:hover {
    background-image: url("../img/btn_opros-hover.webp");
  }
}
@media (min-width: 1440px) {
  .score {
    height: 180px;
    background-image: url("../img/SCORE.webp");
    background-repeat: no-repeat;
    background-size: cover;
  }
  .score__title {
    font-family: "Acrom", sans-serif;
    margin: 0;
    padding-top: 20px;
    color: #fff;
    margin-left: 165px;
    text-transform: uppercase;
    position: relative;
  }
  .score__title::before {
    content: "";
    position: absolute;
    bottom: -107px;
    left: 140px;
    background-color: #1c1c1c;
    width: 15px;
    height: 22px;
  }
  .score__description {
    padding-top: 5px;
    font-family: "Acrom", sans-serif;
    font-size: 31px;
    margin-left: 165px;
    color: #ffae00;
    position: relative;
  }
  .score__description::before {
    content: "деноминация";
    position: absolute;
    font-family: "Acrom", sans-serif;
    color: #fff;
    top: 55px;
    font-size: 14px;
    right: 202px;
    text-transform: uppercase;
  }
  .score__take {
    font-family: "Acrom", sans-serif;
    width: 111px;
    appearance: none;
    cursor: pointer;
    background-image: url("../img/arrow.webp");
    background-position: 100% 70%;
    background-repeat: no-repeat;
    background-color: #262727;
    margin-left: 232px;
    font-size: 19px;
    color: #ffffff;
    font-weight: bold;
    border: none;
    outline: none;
  }

  .offer {
    height: 285px;
    background-image: url("../img/TIMELINE.webp");
    background-repeat: no-repeat;
    background-size: cover;
  }
  .offer__price {
    font-family: "Acrom", sans-serif;
    padding-top: 90px;
    margin-left: 250px;
    font-size: 18px;
    color: #fafafa;
  }
  .offer__price:first-child {
    padding-bottom: 0;
  }
  .offer__price:nth-child(2) {
    padding: 15px 0 0 0;
  }
  .offer__price:nth-child(3) {
    padding: 15px 0 0 0;
  }
  .offer__price:last-child {
    padding: 15px 0 0 0;
  }

  .wrapper {
    display: flex;
    justify-content: space-between;
    margin: 0;
  }

  .pravila {
    outline: none;
    font-size: 0;
    background-image: url("../img/RULES.webp");
    width: 180px;
    height: 55px;
    background-color: transparent;
    border: none;
    margin-top: 0;
    background-repeat: no-repeat;
    cursor: pointer;
  }
  .pravila__pos {
    width: 241px;
  }

  .pay {
    outline: none;
    font-size: 0;
    background-image: url("../img/OFFER.webp");
    width: 165px;
    height: 45px;
    margin-left: -40px;
    margin-top: 10px;
    background-color: transparent;
    border: none;
    background-repeat: no-repeat;
    cursor: pointer;
  }
  .pay__pos {
    width: 150px;
  }
}